//把接口的price字段变成实际显示的钱数：2230 -》 22.30
function realPrice(p) {
    if (!p) {
        return p;
    }
    return (p / 100).toFixed(2) + '';
}

//把输入的价格转换成接口字段：22.30 -》2230
function dataPrice(s) {
    var value = parseFloat(s);
    if (!value) {
        return 0;
    }
    return Math.round(value * 100);
}

