/*时间戳转化为时间格式*/
function dateFormat(time) {
    var type = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'toMinute';

    if (!time) {
        return '';
    }
    var set_time = new Date(time);
    var y = set_time.getFullYear();
    var m = String(set_time.getMonth() + 1);
    var d = String(set_time.getDate());
    var h = String(set_time.getHours());
    var i = String(set_time.getMinutes());
    var s = String(set_time.getSeconds());
    if (m.length === 1) {
        m = "0" + m;
    }
    if (d.length === 1) {
        d = "0" + d;
    }
    if (h.length === 1) {
        h = "0" + h;
    }
    if (i.length === 1) {
        i = "0" + i;
    }
    if (s.length === 1) {
        s = "0" + s;
    }
    if (type === 'toDate') {
        return y + '-' + m + '-' + d;
    } else if (type === 'toHour') {
        return y + '-' + m + '-' + d + ' ' + h;
    } else if (type === 'toMinute') {
        return y + '-' + m + '-' + d + ' ' + h + ':' + i;
    } else if (type === 'toSecond') {
        return y + '-' + m + '-' + d + ' ' + h + ':' + i + ':' + s;
    }
    return '';
}

